import { useState, useEffect } from 'react';

interface SettingsPanelProps {
    splitLength: number;
    setSplitLength: (value: number) => void;
    onClose: () => void;
}

const SettingsPanel = ({ splitLength, setSplitLength, onClose }: SettingsPanelProps) => {
    const [localLength, setLocalLength] = useState(splitLength);

    const handleApply = () => {
        setSplitLength(localLength);
        onClose();
    };

    return (
        <div className="mt-4 p-4 border border-gray-200 rounded-md bg-gray-50">
            <div className="flex justify-between items-center mb-2">
                <h3 className="font-medium">Настройки разбивки</h3>
                <button 
                    onClick={onClose}
                    className="text-gray-500 hover:text-gray-700"
                >
                    &times;
                </button>
            </div>
            <div className="space-y-3">
                <div>
                    <label htmlFor="splitLength" className="block text-sm font-medium text-gray-700 mb-1">
                        Символов в блоке:
                    </label>
                    <input
                        id="splitLength"
                        type="number"
                        min="10"
                        max="500"
                        value={localLength}
                        onChange={(e) => setLocalLength(Number(e.target.value))}
                        className="w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-blue-500 focus:border-blue-500"
                    />
                </div>
                <div className="flex justify-end space-x-2">
                    <button
                        onClick={onClose}
                        className="px-4 py-2 text-sm font-medium text-gray-700 bg-white border border-gray-300 rounded-md hover:bg-gray-50"
                    >
                        Отмена
                    </button>
                    <button
                        onClick={handleApply}
                        className="px-4 py-2 text-sm font-medium text-white bg-blue-600 rounded-md hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500"
                    >
                        Применить
                    </button>
                </div>
            </div>
        </div>
    );
};

export default SettingsPanel;